.container{
    border: 1px solid var(--clr-light);
    border-radius: 5px;
    margin: 0.5rem 0;

    &:hover{
        border-color: var(--clr-main);
    }
    .button{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 0.5rem;
        
        span{
            font-weight: 200;
        }
        small{
            font-weight: 400;
        }

        svg{
            display: flex;
        }
    }
}